import Vue from 'vue'
import Vuex from 'vuex'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import BlogIndex from '../components/BlogIndex'
import BlogHome from '../components/BlogHome'
import BlogArticle from '../components/BlogArticle'
import BlogAbout from '../components/BlogAbout'
import BlogGallary from '../components/BlogGallary'
import ArticleContent from '../components/ArticleContent'
import BlogStudy from '../components/BlogStudy'
import Error403 from '../components/403'
import Error404 from '../components/404'

Vue.use(Router)
Vue.use(Vuex)

export default new Router({
  mode: 'hash',
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'blogindex',
      component: BlogIndex
    }, {
      path: '/home',
      name: 'bloghome',
      component: BlogHome,
      children: [
        {
          // å½“ /user/:id/profile åŒ¹é…æˆåŠŸï¼Œ
          // UserProfile ä¼šè¢«æ¸²æŸ“åœ¨ User çš„ <router-view> ä¸­
          path: '/article',
          component: BlogArticle
        },
        {
          // å½“ /user/:id/profile åŒ¹é…æˆåŠŸï¼Œ
          // UserProfile ä¼šè¢«æ¸²æŸ“åœ¨ User çš„ <router-view> ä¸­
          path: '/study',
          component: BlogStudy
        },
        {
          // å½“ /user/:id/profile åŒ¹é…æˆåŠŸï¼Œ
          // UserProfile ä¼šè¢«æ¸²æŸ“åœ¨ User çš„ <router-view> ä¸­
          path: '/article/:id',
          component: ArticleContent
        },
        {
          // å½“ /user/:id/posts åŒ¹é…æˆåŠŸ
          // UserPosts ä¼šè¢«æ¸²æŸ“åœ¨ User çš„ <router-view> ä¸­
          path: '/gallary',
          component: BlogGallary
        },
        {
          // å½“ /user/:id/posts åŒ¹é…æˆåŠŸ
          // UserPosts ä¼šè¢«æ¸²æŸ“åœ¨ User çš„ <router-view> ä¸­
          path: '/about',
          component: BlogAbout
        }
      ]
    }, {
      path: '/error403',
      component: Error403
    }, {
      path: '/error404',
      component: Error404
    }
  ]
})



