import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    accessToken: '',
    moduleGallery: {
      positionX: 0,
      positionY: 0
    },
    moduleArticle: {
      positionX: 0,
      positionY: 0,
      num: '',
      content: '',
      title: '',
      updateTime: '',
      test: 0
    },
    moduleAbout: {
      positionX: 0,
      positionY: 0
    }
  }
})



